<template>
  <div id="result">
    <a-table
      v-for="(item, imageindex) in result"
      :key="imageindex"
      :columns="columns"
      :data-source="item.decoded"
      :pagination="false"
      bordered
    >
      <template #bodyCell="{ column, index, record }">
        <template v-if="column.dataIndex === 'image' && index === 0">
          <div class="decode-preview">
            <img :src="item.image.src" alt="预览图片" @load="decode(imageindex)" />
            <template v-for="result in item.decoded" :key="result.index">
              <span
                v-if="result.location"
                :style="{
                  left: result.location[0] * item.pointScale + 'px',
                  top: result.location[1] * item.pointScale + 'px'
                }"
              >
                {{ result.index }}
              </span>
            </template>
          </div>
        </template>
        <template v-else-if="column.dataIndex === 'status' && index === 0">
          <div class="decode-status" v-show="item.status === 'decoding'">
            <LoadingOutlined />
            <div>正在解码...</div>
          </div>
          <div class="decode-status" v-show="item.status === 'success'">
            <CheckCircleOutlined class="success-text" />
            <div>解码成功</div>
          </div>
          <div class="decode-status" v-show="item.status === 'failed'">
            <CloseCircleOutlined class="error-text" />
            <div>未能识别</div>
          </div>
        </template>
        <template v-else-if="column.dataIndex === 'action'">
          <a-space v-if="record.index">
            <a-tooltip title="已复制" trigger="click" placement="left">
              <a-button
                type="primary"
                size="small"
                ghost
                :class="`clipboard-btn-${imageindex}`"
                :data-clipboard-text="record.text"
              >
                复制
              </a-button>
            </a-tooltip>
            <a-button type="primary" size="small" @click="openQrEditor(record.text)">美化</a-button>
          </a-space>
        </template>
      </template>
    </a-table>
  </div>
  <teleport to="body">
    <div class="mask" v-show="showQrEditor">
      <div class="container bounce-in">
        <p><CloseCircleOutlined class="close" @click="closeQrEditor" /></p>
        <QrEditor :qrText="qrText">
          <template #contentTitle>编辑二维码内容</template>
          <template #tips>功能描述：编辑二维码内容。</template>
          <template #content>
            <a-textarea
              v-model:value="qrText"
              placeholder="请输入二维码内容"
              :autoSize="{ minRows: 4, maxRows: 6 }"
              :maxlength="3000"
              allowClear
            />
          </template>
        </QrEditor>
      </div>
    </div>
  </teleport>
</template>

<script>
import { LoadingOutlined, CheckCircleOutlined, CloseCircleOutlined } from '@ant-design/icons-vue';
import { reactive, toRefs } from 'vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';

export default {
  name: 'DecodeResult',
  components: {
    LoadingOutlined,
    CheckCircleOutlined,
    CloseCircleOutlined,
    QrEditor
  },
  props: {
    result: Array,
    columns: Array
  },
  emits: ['decode'],
  setup(props, context) {
    let view = reactive({
      qrText: '',
      showQrEditor: false
    });

    function openQrEditor(text) {
      view.showQrEditor = true;
      view.qrText = text;
    }
    function closeQrEditor() {
      view.showQrEditor = false;
      view.qrText = '';
    }
    function decode(index) {
      if (index === props.result.length - 1)
        document.querySelector('#result').scrollIntoView({ behavior: 'smooth' });
      view.showQrEditor = true;closeQrEditor();
      context.emit('decode', index);
    }

    return {
      ...toRefs(view),
      openQrEditor,
      closeQrEditor,
      decode
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.decode-preview {
  display: inline-block;
  max-width: 100%;
  line-height: 0;
  position: relative;
  & > img {
    max-width: 340px;
    max-height: 340px;
    border-radius: 5px;
    vertical-align: middle;
  }
  & > span {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 22px;
    height: 22px;
    line-height: 22px;
    transform: translate(-50%, -50%);
    background: @primary-color;
    border-radius: 50%;
    border: 2px solid #ffffff;
    color: #ffffff;
    font-weight: 600;
    opacity: 0.9;
    box-shadow: 0px 0px 2px 2px @primary-color;
  }
}
.decode-status {
  .anticon {
    font-size: 25px;
  }
}
.mask {
  .container {
    p {
      text-align: center;
      .close {
        font-size: 45px;
        cursor: pointer;
      }
    }
  }
}
</style>
