import { onMounted, onBeforeUnmount, reactive, ref, nextTick } from 'vue';
import { message } from 'ant-design-vue';
import PQueue from 'p-queue/dist';
import ClipboardJS from 'clipboard';
import {
    dragUpload,
    unDragUpload,
    checkImg,
    fileToImage,
    loadImage,
    imageToImageData,
    imageDataToCanvas,
    isUrl,
    isBase64Image,
} from '@/utils/functions';
import request from '@/utils/request';
import decodeWorker from 'worker-loader!@/utils/dcr';

export default function useInitDecoder() {
    let result = reactive([]),
        worker = null,
        key = null,
        queue = new PQueue({ concurrency: 1 }),
        loading = ref(false),
        columns = [
            {
                title: '预览图片',
                dataIndex: 'image',
                width: 380,
                align: 'center',
                customCell: (record, index) => {
                    return { rowSpan: index ? 0 : record.total };
                },
            },
            {
                title: '解码状态',
                dataIndex: 'status',
                width: 90,
                align: 'center',
                customCell: (record, index) => {
                    return { rowSpan: index ? 0 : record.total };
                },
            },
            {
                title: '序号',
                dataIndex: 'index',
                width: 50,
                align: 'center',
            },
            {
                title: '条码类型',
                dataIndex: 'format',
                width: 90,
                align: 'center',
            },
            {
                title: '解码结果',
                dataIndex: 'text',
            },
            {
                title: '操作',
                dataIndex: 'action',
                width: 130,
                align: 'center',
            },
        ];

    function sendToWorker(data) {
        if (!worker) worker = new decodeWorker();
        return new Promise((resolve, reject) => {
            worker.postMessage({ ...data, key, mode: process.env.NODE_ENV });
            worker.onmessage = (event) => resolve(event.data);
            worker.onerror = (event) => resolve(null);
        });
    }
    async function quantizeImage(imageData) {
        let { newImageData } = await sendToWorker({ imageData, action: 'quantize' });
        let canvas = imageDataToCanvas(newImageData);
        let quantized = await loadImage(canvas.toDataURL('image/png'));
        return quantized;
    }
    function isDecoding() {
        let status = result.some((item) => item.status === 'decoding');
        if (status || loading.value) message.warning('请等待上一个解码任务完成!');
        return status || loading.value;
    }
    async function loadFile(file) {
        if (isDecoding()) return;

        result.length = 0;
        if (!checkImg(file)) return;
        let image = await fileToImage(file);
        result.push({
            image: image,
            status: 'decoding',
            decoded: [{}],
        });
        dblog('loadFile', result);
    }
    async function loadFiles(files) {
        if (isDecoding()) return;

        files = [...files];
        result.length = 0;
        if (files.some((file) => !checkImg(file))) return;
        for (let i = 0; i < files.length; i++) {
            let image = await fileToImage(files[i]);
            result.push({
                image: image,
                status: 'decoding',
                decoded: [{}],
            });
        }
        dblog('loadFiles', result);
    }
    async function loadUrl(url) {
        if (!url || isDecoding()) return;
        result.length = 0;
        let isBase64 = await isBase64Image(url);
        if (!isUrl(url) && !isBase64) return message.warning('图片链接必须是 http(s):// 开头 或 base64 格式');

        let src = null;
        if (isBase64) src = url;
        else {
            loading.value = true;
            try {
                ({ src } = await request.getUrlImage(url));
            } catch (error) {
            } finally {
                loading.value = false;
            }
        }
        if (!src) return;

        let image = await loadImage(src);
        result.push({
            image: image,
            status: 'decoding',
            decoded: [{}],
        });
        dblog('loadUrl', result);
    }
    async function decodeIndex(i) {
        let max = 1200,
            imageData = null,
            quantized = null,
            data = null,
            item = result[i],
            imgSize = Math.max(item.image.width, item.image.height),
            toSize = imgSize > max ? max : imgSize;

        while ((!data || !data.decoded.length) && toSize <= max) {
            dblog('try originImage', toSize);
            imageData = await imageToImageData(item.image, toSize);
            toSize = data ? toSize * 1.3 : 400;
            data = await sendToWorker({ imageData });
        }

        toSize = 400;
        while (!data.decoded.length && toSize <= max) {
            if (!quantized) quantized = await quantizeImage(imageData);
            dblog('try quantizeImage', toSize);
            imageData = await imageToImageData(quantized, toSize);
            toSize = toSize * 1.3;
            data = await sendToWorker({ imageData });
        }

        if (data.decoded.length) {
            item.status = 'success';
            item.decoded = data.decoded;
            item.pointScale = (imgSize < 340 ? imgSize : 340) / Math.max(imageData.width, imageData.height);
            nextTick(() => item.status === 'success' && new ClipboardJS(`.clipboard-btn-${i}`));
        } else {
            item.status = 'failed';
        }
        return item.status;
    }
    function startDecode(index) {
        (async () => {
            await queue.add(() => decodeIndex(index));
        })();
    }
    function initDecoder() {
        dragUpload('body', loadFile);
    }
    function killDecoder() {
        unDragUpload('body');
        worker && worker.terminate();
    }

    onMounted(async () => {
        ({ key } = await request.getDecoder());
        if (!localStorage.getItem('decoder') && window.Worker) {
            let src = `data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20version%3D%221.1%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%20xmlns%3Asvgjs%3D%22http%3A%2F%2Fsvgjs.dev%2Fsvgjs%22%20width%3D%22500%22%20height%3D%22500%22%20viewBox%3D%220%200%20500%20500%22%3E%3Crect%20width%3D%22500%22%20height%3D%22500%22%20x%3D%220%22%20y%3D%220%22%20fill%3D%22%23ffffff%22%3E%3C%2Frect%3E%3Cdefs%3E%3C%2Fdefs%3E%3Csvg%20width%3D%22460%22%20height%3D%22460%22%20viewBox%3D%220%200%20250%20250%22%20x%3D%2220%22%20y%3D%2220%22%20style%3D%22overflow%3A%20visible%3B%22%3E%3Cdefs%3E%3Cmask%3E%3Cg%20id%3D%22SvgjsG1042%22%3E%3Cpath%20shape-rendering%3D%22crispEdges%22%20d%3D%22M90%2C0h10v10h-10v-10ZM120%2C0h10v10h-10v-10ZM130%2C0h10v10h-10v-10ZM150%2C0h10v10h-10v-10ZM80%2C10h10v10h-10v-10ZM120%2C10h10v10h-10v-10ZM130%2C10h10v10h-10v-10ZM80%2C20h10v10h-10v-10ZM90%2C20h10v10h-10v-10ZM110%2C20h10v10h-10v-10ZM140%2C30h10v10h-10v-10ZM150%2C30h10v10h-10v-10ZM160%2C30h10v10h-10v-10ZM110%2C40h10v10h-10v-10ZM120%2C40h10v10h-10v-10ZM140%2C40h10v10h-10v-10ZM150%2C40h10v10h-10v-10ZM160%2C40h10v10h-10v-10ZM100%2C50h10v10h-10v-10ZM130%2C50h10v10h-10v-10ZM80%2C60h10v10h-10v-10ZM100%2C60h10v10h-10v-10ZM120%2C60h10v10h-10v-10ZM140%2C60h10v10h-10v-10ZM160%2C60h10v10h-10v-10ZM110%2C70h10v10h-10v-10ZM120%2C70h10v10h-10v-10ZM160%2C70h10v10h-10v-10ZM10%2C80h10v10h-10v-10ZM20%2C80h10v10h-10v-10ZM30%2C80h10v10h-10v-10ZM50%2C80h10v10h-10v-10ZM60%2C80h10v10h-10v-10ZM100%2C80h10v10h-10v-10ZM120%2C80h10v10h-10v-10ZM130%2C80h10v10h-10v-10ZM160%2C80h10v10h-10v-10ZM220%2C80h10v10h-10v-10ZM230%2C80h10v10h-10v-10ZM0%2C90h10v10h-10v-10ZM10%2C90h10v10h-10v-10ZM30%2C90h10v10h-10v-10ZM70%2C90h10v10h-10v-10ZM80%2C90h10v10h-10v-10ZM130%2C90h10v10h-10v-10ZM140%2C90h10v10h-10v-10ZM160%2C90h10v10h-10v-10ZM170%2C90h10v10h-10v-10ZM190%2C90h10v10h-10v-10ZM230%2C90h10v10h-10v-10ZM0%2C100h10v10h-10v-10ZM50%2C100h10v10h-10v-10ZM60%2C100h10v10h-10v-10ZM80%2C100h10v10h-10v-10ZM100%2C100h10v10h-10v-10ZM110%2C100h10v10h-10v-10ZM130%2C100h10v10h-10v-10ZM200%2C100h10v10h-10v-10ZM10%2C110h10v10h-10v-10ZM20%2C110h10v10h-10v-10ZM40%2C110h10v10h-10v-10ZM90%2C110h10v10h-10v-10ZM110%2C110h10v10h-10v-10ZM120%2C110h10v10h-10v-10ZM150%2C110h10v10h-10v-10ZM170%2C110h10v10h-10v-10ZM210%2C110h10v10h-10v-10ZM220%2C110h10v10h-10v-10ZM20%2C120h10v10h-10v-10ZM40%2C120h10v10h-10v-10ZM50%2C120h10v10h-10v-10ZM60%2C120h10v10h-10v-10ZM70%2C120h10v10h-10v-10ZM90%2C120h10v10h-10v-10ZM110%2C120h10v10h-10v-10ZM140%2C120h10v10h-10v-10ZM170%2C120h10v10h-10v-10ZM180%2C120h10v10h-10v-10ZM190%2C120h10v10h-10v-10ZM200%2C120h10v10h-10v-10ZM220%2C120h10v10h-10v-10ZM230%2C120h10v10h-10v-10ZM240%2C120h10v10h-10v-10ZM10%2C130h10v10h-10v-10ZM20%2C130h10v10h-10v-10ZM40%2C130h10v10h-10v-10ZM80%2C130h10v10h-10v-10ZM90%2C130h10v10h-10v-10ZM100%2C130h10v10h-10v-10ZM110%2C130h10v10h-10v-10ZM120%2C130h10v10h-10v-10ZM140%2C130h10v10h-10v-10ZM150%2C130h10v10h-10v-10ZM160%2C130h10v10h-10v-10ZM180%2C130h10v10h-10v-10ZM190%2C130h10v10h-10v-10ZM200%2C130h10v10h-10v-10ZM240%2C130h10v10h-10v-10ZM10%2C140h10v10h-10v-10ZM20%2C140h10v10h-10v-10ZM30%2C140h10v10h-10v-10ZM40%2C140h10v10h-10v-10ZM50%2C140h10v10h-10v-10ZM60%2C140h10v10h-10v-10ZM70%2C140h10v10h-10v-10ZM80%2C140h10v10h-10v-10ZM90%2C140h10v10h-10v-10ZM100%2C140h10v10h-10v-10ZM120%2C140h10v10h-10v-10ZM130%2C140h10v10h-10v-10ZM160%2C140h10v10h-10v-10ZM180%2C140h10v10h-10v-10ZM200%2C140h10v10h-10v-10ZM220%2C140h10v10h-10v-10ZM230%2C140h10v10h-10v-10ZM0%2C150h10v10h-10v-10ZM30%2C150h10v10h-10v-10ZM40%2C150h10v10h-10v-10ZM70%2C150h10v10h-10v-10ZM80%2C150h10v10h-10v-10ZM160%2C150h10v10h-10v-10ZM190%2C150h10v10h-10v-10ZM200%2C150h10v10h-10v-10ZM240%2C150h10v10h-10v-10ZM20%2C160h10v10h-10v-10ZM60%2C160h10v10h-10v-10ZM90%2C160h10v10h-10v-10ZM120%2C160h10v10h-10v-10ZM140%2C160h10v10h-10v-10ZM150%2C160h10v10h-10v-10ZM160%2C160h10v10h-10v-10ZM170%2C160h10v10h-10v-10ZM180%2C160h10v10h-10v-10ZM190%2C160h10v10h-10v-10ZM200%2C160h10v10h-10v-10ZM210%2C160h10v10h-10v-10ZM220%2C160h10v10h-10v-10ZM230%2C160h10v10h-10v-10ZM240%2C160h10v10h-10v-10ZM80%2C170h10v10h-10v-10ZM100%2C170h10v10h-10v-10ZM110%2C170h10v10h-10v-10ZM130%2C170h10v10h-10v-10ZM160%2C170h10v10h-10v-10ZM200%2C170h10v10h-10v-10ZM220%2C170h10v10h-10v-10ZM240%2C170h10v10h-10v-10ZM120%2C180h10v10h-10v-10ZM160%2C180h10v10h-10v-10ZM180%2C180h10v10h-10v-10ZM200%2C180h10v10h-10v-10ZM220%2C180h10v10h-10v-10ZM230%2C180h10v10h-10v-10ZM240%2C180h10v10h-10v-10ZM80%2C190h10v10h-10v-10ZM100%2C190h10v10h-10v-10ZM120%2C190h10v10h-10v-10ZM130%2C190h10v10h-10v-10ZM160%2C190h10v10h-10v-10ZM200%2C190h10v10h-10v-10ZM90%2C200h10v10h-10v-10ZM130%2C200h10v10h-10v-10ZM140%2C200h10v10h-10v-10ZM160%2C200h10v10h-10v-10ZM170%2C200h10v10h-10v-10ZM180%2C200h10v10h-10v-10ZM190%2C200h10v10h-10v-10ZM200%2C200h10v10h-10v-10ZM210%2C200h10v10h-10v-10ZM80%2C210h10v10h-10v-10ZM90%2C210h10v10h-10v-10ZM100%2C210h10v10h-10v-10ZM170%2C210h10v10h-10v-10ZM180%2C210h10v10h-10v-10ZM200%2C210h10v10h-10v-10ZM210%2C210h10v10h-10v-10ZM220%2C210h10v10h-10v-10ZM230%2C210h10v10h-10v-10ZM240%2C210h10v10h-10v-10ZM80%2C220h10v10h-10v-10ZM90%2C220h10v10h-10v-10ZM110%2C220h10v10h-10v-10ZM140%2C220h10v10h-10v-10ZM150%2C220h10v10h-10v-10ZM160%2C220h10v10h-10v-10ZM170%2C220h10v10h-10v-10ZM180%2C220h10v10h-10v-10ZM200%2C220h10v10h-10v-10ZM220%2C220h10v10h-10v-10ZM230%2C220h10v10h-10v-10ZM80%2C230h10v10h-10v-10ZM90%2C230h10v10h-10v-10ZM110%2C230h10v10h-10v-10ZM120%2C230h10v10h-10v-10ZM180%2C230h10v10h-10v-10ZM200%2C230h10v10h-10v-10ZM220%2C230h10v10h-10v-10ZM90%2C240h10v10h-10v-10ZM100%2C240h10v10h-10v-10ZM120%2C240h10v10h-10v-10ZM140%2C240h10v10h-10v-10ZM170%2C240h10v10h-10v-10ZM180%2C240h10v10h-10v-10ZM190%2C240h10v10h-10v-10ZM200%2C240h10v10h-10v-10ZM210%2C240h10v10h-10v-10ZM220%2C240h10v10h-10v-10ZM230%2C240h10v10h-10v-10ZM240%2C240h10v10h-10v-10Z%22%3E%3C%2Fpath%3E%3C%2Fg%3E%3C%2Fmask%3E%3Csvg%20width%3D%2270%22%20height%3D%2270%22%20viewBox%3D%220%200%2070%2070%22%20id%3D%22SvgjsSvg1041%22%3E%3Cpath%20d%3D%22M70%2C70H0V0h70V70z%20M10%2C60h50V10H10V60z%20M20%2C20h30v30H20V20z%22%20shape-rendering%3D%22crispEdges%22%3E%3C%2Fpath%3E%3C%2Fsvg%3E%3Cmask%3E%3Cg%20id%3D%22SvgjsG1043%22%3E%3Cuse%20xlink%3Ahref%3D%22%23SvgjsSvg1041%22%3E%3C%2Fuse%3E%3Cuse%20xlink%3Ahref%3D%22%23SvgjsSvg1041%22%20transform%3D%22matrix(-1%2C0%2C0%2C1%2C250%2C0)%22%3E%3C%2Fuse%3E%3Cuse%20xlink%3Ahref%3D%22%23SvgjsSvg1041%22%20transform%3D%22matrix(1%2C0%2C0%2C-1%2C0%2C250)%22%3E%3C%2Fuse%3E%3C%2Fg%3E%3C%2Fmask%3E%3C%2Fdefs%3E%3Cuse%20xlink%3Ahref%3D%22%23SvgjsG1042%22%3E%3C%2Fuse%3E%3Cuse%20xlink%3Ahref%3D%22%23SvgjsG1043%22%3E%3C%2Fuse%3E%3C%2Fsvg%3E%3C%2Fsvg%3E`,
                image = await loadImage(src),
                imageData = await imageToImageData(image, 500),
                data = await sendToWorker({ imageData });
            if (data && data.decoded && data.decoded.length) localStorage.setItem('decoder', 'zbar');
        }
        (!window.Worker || !localStorage.getItem('decoder')) && window.unsupport();
        initDecoder();
    });
    onBeforeUnmount(() => killDecoder());
    window.onbeforeunload = () => killDecoder();

    return {
        loading,
        result,
        columns,
        loadFile,
        loadFiles,
        loadUrl,
        startDecode,
        decodeIndex,
    };
}
